.text-left{text-align:left}.playsets{background-color:#221f43;background-image:url("../../assets/images/fondo.png");background-repeat:no-repeat;background-size:cover;color:#fff;min-height:90vh;margin-top:130px}.playsets ::v-deep .modal-content{width:100%;background:#0f0d2a;color:#fff;padding:20px}.playsets ::v-deep .modal-content strong{color:#fff}.playsets ::v-deep .modal-content img{max-width:320px;display:block;margin:30px auto}.playsets ::v-deep .modal-content .title{color:#fff;text-transform:uppercase;margin-top:20px;font-size:30px}.playsets ::v-deep .modal-content .description{padding:30px;text-align:justify}.playsets ::v-deep .modal-content button{background:rgba(0,0,0,0);border:1px solid #fff;color:#fff;border-radius:30px;height:50px;padding:0 30px}.playsets ::v-deep .modal-content button .icon{padding:5px 25px}.playsets ::v-deep .modal-content button .icon i{color:#00d8cd;opacity:.6}.playsets .bg{min-height:50vh;background-size:cover;background-repeat:no-repeat;background-position:center}.playsets .bg.bg-1{background-image:url("../../assets/images/carousel1.jpeg")}.playsets .bg.bg-2{background-image:url("../../assets/images/carousel2.jpeg")}.playsets .bg.bg-3{background-image:url("../../assets/images/carousel3.jpeg")}.playsets .bg.bg-4{background-image:url("../../assets/images/carousel4.jpeg")}.columns{padding-top:20px;padding-left:50px}@media(max-width: 414px){.columns{width:105%;padding-left:0}}.playset{-webkit-box-shadow:10px 10px 5px 0px rgba(0,0,0,.75);-moz-box-shadow:10px 10px 5px 0px rgba(0,0,0,.75);box-shadow:10px 10px 5px 0px rgba(0,0,0,.75);background-color:#443c8f;margin:10px;padding:0px;width:30%;height:100%;opacity:.9;transition:all .6s ease;border-radius:5px}.playset .card{box-shadow:0 1px 5px rgba(0,0,0,.6);transition-duration:300ms;transition-property:transform,box-shadow;transition-timing-function:ease-out;transform:rotate3d(0)}.playset .card:hover{transition-duration:150ms;box-shadow:0 5px 20px 5px rgba(0,0,0,.2666666667)}.playset .card-header{height:60px;padding:10px;background-color:#443c8f}.playset .card-image{min-height:310px;background-color:#443c8f}.playset p{color:#fff;text-transform:uppercase;font-size:20px;text-align:center;width:100%;margin-bottom:10px}.playset img{background-color:#221f43}.playset:hover{opacity:1;cursor:pointer}.playset.disabled{opacity:.56;cursor:not-allowed}.playset.disabled img{-webkit-filter:grayscale(100%);filter:grayscale(100%)}@media(max-width: 414px){.playset{width:80%;margin:20px auto}}